// üöÄ PARALLEL RENDERER - UTILIZEAZƒÇ TOATE CORES-URILE REAL!
import { Worker } from 'worker_threads';
import * as path from 'path';
import * as fs from 'fs/promises';
import { spawn } from 'child_process';

interface SegmentJob {
  segmentIndex: number;
  clips: any[];
  outputPath: string;
  segmentDuration: number;
  totalSegments: number;
  editSpec: any;
}

export class ParallelRenderer {
  private maxWorkers = 16; // üöÄ 16 CORES PARALELI
  private activeWorkers: Worker[] = [];
  private jobQueue: SegmentJob[] = [];
  private completedSegments: string[] = [];

  constructor() {
    console.log(`üöÄ Parallel Renderer initialized with ${this.maxWorkers} workers`);
  }

  async renderParallel(editSpec: any): Promise<string> {
    const startTime = Date.now();
    console.log(`üé¨ Starting PARALLEL rendering with ${this.maxWorkers} cores`);

    // üöÄ √éMPARTE VIDEO-UL √éN SEGMENTE
    const segments = this.splitIntoSegments(editSpec);
    console.log(`üìπ Split into ${segments.length} segments for parallel processing`);

    // üöÄ PROCESEAZƒÇ TOATE SEGMENTELE √éN PARALEL
    const segmentPaths = await this.processSegmentsParallel(segments, editSpec);

    // üöÄ CONCATENEAZƒÇ SEGMENTELE
    const finalPath = await this.concatenateSegments(segmentPaths, editSpec.outPath);

    const processingTime = (Date.now() - startTime) / 1000;
    console.log(`‚úÖ PARALLEL rendering completed in ${processingTime}s using ${this.maxWorkers} cores`);

    return finalPath;
  }

  private splitIntoSegments(editSpec: any): any[] {
    const segments: any[] = [];
    const totalClips = editSpec.clips.length;
    const clipsPerSegment = Math.max(1, Math.ceil(totalClips / this.maxWorkers));

    for (let i = 0; i < totalClips; i += clipsPerSegment) {
      const segmentClips = editSpec.clips.slice(i, i + clipsPerSegment);
      segments.push({
        segmentIndex: segments.length,
        clips: segmentClips,
        startClipIndex: i,
        endClipIndex: Math.min(i + clipsPerSegment - 1, totalClips - 1)
      });
    }

    console.log(`üìä Split ${totalClips} clips into ${segments.length} segments (${clipsPerSegment} clips each)`);
    return segments;
  }

  private async processSegmentsParallel(segments: any[], editSpec: any): Promise<string[]> {
    const promises: Promise<string>[] = [];

    console.log(`üöÄ Launching ${segments.length} parallel workers...`);

    for (const segment of segments) {
      const segmentEditSpec = {
        ...editSpec,
        clips: segment.clips,
        outPath: `/app/temp/segment_${segment.segmentIndex}.mp4`
      };

      // üöÄ LANSEAZƒÇ WORKER PENTRU ACEST SEGMENT
      const promise = this.processSegmentWithWorker(segmentEditSpec, segment.segmentIndex);
      promises.push(promise);
    }

    // üöÄ A»òTEAPTƒÇ CA TOATE SEGMENTELE SƒÇ SE TERMINE
    const segmentPaths = await Promise.all(promises);
    console.log(`‚úÖ All ${segmentPaths.length} segments completed in parallel!`);

    return segmentPaths;
  }

  private async processSegmentWithWorker(segmentEditSpec: any, segmentIndex: number): Promise<string> {
    return new Promise((resolve, reject) => {
      // üöÄ FIX for ES modules - get current directory
      const __filename = new URL(import.meta.url).pathname;
      const __dirname = path.dirname(__filename);
      const workerPath = path.join(__dirname, 'segment-worker.js');
      const worker = new Worker(workerPath, {
        workerData: { segmentEditSpec, segmentIndex }
      });

      console.log(`üî• Worker ${segmentIndex} started (PID: ${worker.threadId})`);

      worker.on('message', (result) => {
        if (result.success) {
          console.log(`‚úÖ Worker ${segmentIndex} completed: ${result.outputPath}`);
          resolve(result.outputPath);
        } else {
          console.error(`‚ùå Worker ${segmentIndex} failed:`, result.error);
          reject(new Error(result.error));
        }
      });

      worker.on('error', (error) => {
        console.error(`üí• Worker ${segmentIndex} crashed:`, error);
        reject(error);
      });

      worker.on('exit', (code) => {
        if (code !== 0) {
          console.error(`‚ö†Ô∏è  Worker ${segmentIndex} exited with code ${code}`);
        }
      });

      this.activeWorkers.push(worker);
    });
  }

  private async concatenateSegments(segmentPaths: string[], outputPath: string): Promise<string> {
    console.log(`üîó Concatenating ${segmentPaths.length} segments with FFmpeg...`);

    // üöÄ CREAZƒÇ LISTA PENTRU FFmpeg CONCAT
    const concatList = segmentPaths.map(path => `file '${path}'`).join('\n');
    const concatFile = '/app/temp/concat_list.txt';
    
    await fs.writeFile(concatFile, concatList);

    return new Promise((resolve, reject) => {
      // üöÄ FFmpeg CONCAT CU TOATE CORES-URILE
      const ffmpegArgs = [
        '-f', 'concat',
        '-safe', '0',
        '-i', concatFile,
        '-c', 'copy', // üöÄ Copy fƒÉrƒÉ re-encoding
        '-threads', '16', // üöÄ 16 threads pentru concat
        '-y',
        outputPath
      ];

      console.log(`üé¨ FFmpeg concat command: ffmpeg ${ffmpegArgs.join(' ')}`);

      const ffmpeg = spawn('/usr/local/ffmpeg/bin/ffmpeg', ffmpegArgs);

      ffmpeg.on('close', (code) => {
        if (code === 0) {
          console.log(`‚úÖ Concatenation completed: ${outputPath}`);
          this.cleanup(segmentPaths, concatFile);
          resolve(outputPath);
        } else {
          console.error(`‚ùå FFmpeg concat failed with code: ${code}`);
          reject(new Error(`FFmpeg concat failed with code: ${code}`));
        }
      });

      ffmpeg.on('error', (error) => {
        console.error('üí• FFmpeg concat error:', error);
        reject(error);
      });
    });
  }

  private async cleanup(segmentPaths: string[], concatFile: string) {
    console.log(`üßπ Cleaning up ${segmentPaths.length} temporary segments...`);
    
    try {
      // üöÄ »òTERGE SEGMENTELE TEMPORARE
      for (const segmentPath of segmentPaths) {
        await fs.unlink(segmentPath).catch(() => {});
      }
      
      // üöÄ »òTERGE LISTA CONCAT
      await fs.unlink(concatFile).catch(() => {});
      
      console.log(`‚úÖ Cleanup completed`);
    } catch (error) {
      console.warn('‚ö†Ô∏è  Cleanup warning:', error);
    }
  }

  destroy() {
    // üöÄ TERMINATE TOATE WORKERS-II
    this.activeWorkers.forEach(worker => {
      worker.terminate();
    });
    this.activeWorkers = [];
    console.log(`üõë All workers terminated`);
  }
}

export default ParallelRenderer;